import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';

export default function Dropdown(props) {
    const lang = props.lang
    return (
        <div className="form-group dropdown">
            <label htmlFor={props.obj.category}>{props.obj.category}</label>
            <select class="form-control" name={props.obj.category} id={props.obj.category}>
                {props.obj.values && props.obj.values.map(value => <option value={value.title[lang]} key={value._id} >{value.title[lang]}</option>)}
            </select>
        </div>
    )
}
