import React, {useEffect, useState} from 'react'
import {Link} from 'react-router-dom'

import 'bootstrap/dist/css/bootstrap.min.css';
import {Nav, Container} from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCogs } from '@fortawesome/free-solid-svg-icons'


export default function NavBar(props) {

    const [listCat, setListCat] = useState([])
    const [lang, setLang] = useState('')

    //setLang(props.lang)

    const navStyle = {
        textDecoration: 'none'
    }

    useEffect(() => {
    fetch('https://studifix-mock-api-default-rtdb.europe-west1.firebasedatabase.app/.json')
    .then(response => response.json())
    .then(items => setListCat(items))

    setLang(props.lang)

    }, [])

    return (
        <Nav className="filter__nav navbar-expand-lg navbar-light bg-light">
            <div>Studifix</div>
                {listCat.map(item =><Nav.Item className="nav__item"><Link className="navbar-brand nav__item--link" style={navStyle} to={`/filter/${item.category}`}><h3>{item.description[lang]}</h3></Link></Nav.Item>)}
            <FontAwesomeIcon icon={faCogs} />
        </Nav>
    )
}

