import React, {useEffect, useState} from 'react'
import Dropdown from './Dropdown'
import TextArea from './TextArea'
import {Route, Switch} from 'react-router-dom'


function Filter(props) {
  const [listCat, setListCat] = useState([])

  useEffect(() => {
    console.log('inside useEffect') 
  
    fetch('https://studifix-mock-api-default-rtdb.europe-west1.firebasedatabase.app/.json')
    .then(response => response.json())
    .then(items => setListCat(items))

    
  }, [])
  
  console.log(listCat.map(item => item.fieldType))

  return (
    <Switch>
      {listCat.map(item => {
        switch(item.fieldType){
          case "dropdown": return (<Route path={`/filter/${item.category}`}><Dropdown lang={props.lang} obj={item}/></Route>); 
          break;
          case "textfield": return (<Route path={`/filter/${item.category}`}><TextArea lang={props.lang} obj={item}/></Route>); 
          break;
          default: console.log(item.fieldType);

        }
      }
      )}
    </Switch>
  );
}

export default Filter;
